import React, {useContext} from 'react';
import {Context} from "context";
import {Box, List, ListItemText} from "@mui/material";
import styles from './FooterToDo.module.css';
import {STATUS} from "enums";

export const FooterToDo = () => {
    const {removeTask, countItems, setFilter, filter } = useContext(Context)

    const onAllChangeFilter = () => {
        setFilter && setFilter(STATUS.ALL)
    }
    const onActiveChangeFilter = () => {
        setFilter && setFilter(STATUS.ACTIVE)
    }
    const onCompletedChangeFilter = () => {
        setFilter && setFilter(STATUS.COMPLETED)
    }

        return (
        <List className={styles.containerFooter}>
            <Box>
                <ListItemText className={styles.textItems} secondary={`${countItems} items left`}/>
            </Box>
            <Box className={styles.buttonWrapper}>
                <ListItemText onClick={onAllChangeFilter}
                              className={filter === STATUS.ALL ? styles.active : ""}
                              secondary="All"/>
                <ListItemText onClick={onActiveChangeFilter}
                              className={filter === STATUS.ACTIVE ? styles.active : ""}
                              secondary="Active"/>
                <ListItemText onClick={onCompletedChangeFilter}
                              className={filter === STATUS.COMPLETED ? styles.active : ""}
                              secondary="Completed"/>
            </Box>
            <Box className={styles.textClearCompleted}>
                <ListItemText onClick={removeTask}
                              secondary="Clear completed"/>
            </Box>
        </List>
    );
};

