import React, {FC} from 'react';
import {ListItem, ListItemIcon, ListItemText} from "@mui/material";
import CheckCircleOutlineRoundedIcon from '@mui/icons-material/CheckCircleOutlineRounded';
import RadioButtonUncheckedOutlinedIcon from '@mui/icons-material/RadioButtonUncheckedOutlined';
import styles from './ItemsToDo.module.css';

type ItemToDoType = {
    id: string
    title: string
    isDone: boolean
    handelToggle: (id: string, isDone: boolean) => void
}

export const ItemToDo: FC<ItemToDoType> = ({id, title, isDone, handelToggle}) => {
    return (
        <ListItem divider component="div">
            <ListItemIcon className={styles.itemIcon}
                          onClick={() => {
                handelToggle(id, isDone)}}>
                {isDone
                    ? <CheckCircleOutlineRoundedIcon
                        fontSize="large"
                        className={styles.checkedActive}
                    />
                    : <RadioButtonUncheckedOutlinedIcon
                        fontSize="large"
                        className={styles.checkedDisable}
                    />}
                <ListItemText primary={title}
                              className={isDone ? styles.strike : ""}
                              sx={{marginLeft: 2}}/>
            </ListItemIcon>
        </ListItem>

    );
}


