import React, {ChangeEvent, KeyboardEvent, FC, useState, useContext} from 'react';
import {Context} from "context";
import {InputBase, ListItem} from "@mui/material";
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import styles from './FormToDo.module.css';

type ItemForm = {
    addTask: (value: string) => void
}

export const FormToDo: FC<ItemForm> = ({addTask}) => {
    const {onOpenClick} = useContext(Context)
    const [value, setValue] = useState('')

    const onChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        setValue(event.currentTarget.value)
    }

    const onHandlerKeyDown = (event: KeyboardEvent<HTMLInputElement>) => {
        if (event.key === 'Enter') {
            addTask(value)
            setValue('')
        }
    }

    return (
        <ListItem divider className={styles.containerForm}>
            <KeyboardArrowDownIcon
                color='disabled'
                fontSize="large"
                onClick={onOpenClick}/>
            <InputBase
                className={styles.input}
                value={value}
                onChange={onChangeHandler}
                onKeyDown={onHandlerKeyDown}
                placeholder="What needs to be done?"
            />
        </ListItem>
    );
};

