import React, {useEffect, useState} from 'react';
import {v1} from "uuid";
import { Context } from 'context';
import {STATUS} from "../../enums";
import {FormToDo} from "./FormToDo";
import {ItemToDo} from "./ItemsToDo";
import {FooterToDo} from "./FooterToDo";
import {Collapse, Container, Paper} from "@mui/material"

export const tasksData = [
    {
        id: v1(),
        title: ' ',
        isDone: false,
    },
    {
        id: v1(),
        title: ' ',
        isDone: true,
    },
    {
        id: v1(),
        title: ' ',
        isDone: false,
    },
]

export type TaskType = {
    id: string
    title: string
    isDone: boolean
}

export type FilterValuesType = STATUS.ALL | STATUS.ACTIVE | STATUS.COMPLETED

export const Todolist = () => {
    const [tasks, setTasks] = useState<TaskType[]>(tasksData )
    const [open, setOpen] = useState(false);
    let [filter, setFilter] = useState<FilterValuesType>(STATUS.ALL)

    const onOpenClick = () => {
        setOpen(!open);
    };

    const addTask = (value: string) => {
        if (value) {
            const newTask = {
                id: v1(),
                title: value,
                isDone: false
            }
            setTasks([newTask, ...tasks])
        }
    }

    const onHandelToggle = (id: string, isDone: boolean) => {
        setTasks(
            tasks.map((task) => task.id === id ? {...task, isDone: !isDone} : {...task})
        )
    }

    const removeTask = () => {
        setTasks([...tasks.filter((task) => !task.isDone)])
    }

    const countItems = tasks.filter((task) => !task.isDone).length

    let tasksFilter = tasks

    if (filter === STATUS.ACTIVE) {
        tasksFilter = tasks.filter(task => !task.isDone);
    }
    if (filter === STATUS.COMPLETED) {
        tasksFilter = tasks.filter(task => task.isDone);
    }

    useEffect(() => {
        let getFromLS = localStorage.getItem('tasks')
        if (getFromLS) {
            let data = JSON.parse(getFromLS)
            setTasks(data)
        }
    }, [])

    useEffect(() => {
        localStorage.setItem('tasks', JSON.stringify(tasks))
    }, [tasks])

    return (
        <Context.Provider
            value={{removeTask, addTask, onOpenClick, open, countItems, setFilter, filter}}>
        <Container data-testid="content" maxWidth="sm">
            <Paper elevation={3}>
                <FormToDo addTask={addTask} />
                <Collapse in={open} timeout="auto" unmountOnExit>
                    {tasksFilter.map((task) => {
                        return <ItemToDo
                            key={task.id}
                            id={task.id}
                            title={task.title}
                            isDone={task.isDone}
                            handelToggle={onHandelToggle}
                        />
                    })}
                </Collapse>
                <FooterToDo />
            </Paper>
        </Container>
        </Context.Provider>
    );
};


