import {createContext} from "react";
import {FilterValuesType, TaskType} from "../components/Todolist";

type ContextTypes = {
    tasks:TaskType[]
    removeTask: () => void
    addTask: (value: string) => void
    onOpenClick: () => void
    countItems: number
    open: boolean
    setFilter: (filter: FilterValuesType) => void
    filter: FilterValuesType
}

export const Context = createContext<Partial<ContextTypes>>({});